import React, { useEffect, useState } from 'react';
import { useParams, Link } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import { 
  Container, 
  Typography, 
  Paper, 
  Chip, 
  Button, 
  Box,
  Avatar
} from '@material-ui/core';
import { ArrowBack } from '@material-ui/icons';
import ReactMarkdown from 'react-markdown';
import rehypeSanitize from 'rehype-sanitize';
import { defaultSchema } from 'hast-util-sanitize';
import remarkGfm from 'remark-gfm';
import blogManager from '../../utils/blogManager';
import { LogoLink } from '../logo/LogoLink';
import { ThemeToggle } from '../theme/ThemeToggle';
import { SocialIcons } from '../content/SocialIcons';
import { SpeedDials } from '../speedDial/SpeedDial';
import { TopNavbar } from '../nav/TopNavbar';
import { Hidden } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexDirection: 'column',
    minHeight: '100vh',
    backgroundColor: theme.palette.background.default,
    color: theme.palette.text.primary,
    position: 'relative',
  },
  contentWrapper: {
    marginTop: '6rem',
    marginBottom: '3rem',
    position: 'relative',
    zIndex: 1,
  },
  hero: {
    padding: theme.spacing(4),
    background: `linear-gradient(90deg, #00bfbf 0%, #0077b6 100%)`,
    color: '#fff',
    borderRadius: theme.shape.borderRadius,
    marginBottom: theme.spacing(4),
    textAlign: 'center',
  },
  headerImage: {
    width: '100%',
    height: 'auto',
    maxHeight: 400,
    objectFit: 'cover',
    objectPosition: 'center',
    borderRadius: theme.shape.borderRadius,
    marginBottom: theme.spacing(2),
    display: 'block',
    margin: '0 auto',
  },
  meta: {
    margin: theme.spacing(2, 0),
    display: 'flex',
    justifyContent: 'center',
    gap: theme.spacing(2),
    flexWrap: 'wrap',
    alignItems: 'center',
  },
  tag: {
    margin: theme.spacing(0.5),
    background: theme.palette.type === 'dark' ? 'rgba(255,255,255,0.08)' : 'rgba(0,0,0,0.06)',
    color: theme.palette.text.primary,
    fontWeight: 600,
  },
  content: {
    padding: theme.spacing(3),
    background: theme.palette.background.paper,
    borderRadius: theme.shape.borderRadius,
    boxShadow: theme.shadows[1],
    fontSize: '1.1rem',
    lineHeight: 1.8,
    color: theme.palette.text.primary,
    '& h1, & h2, & h3': {
      color: theme.palette.primary.main,
      marginTop: theme.spacing(3),
      marginBottom: theme.spacing(1),
    },
    '& a': {
      color: theme.palette.primary.main,
      textDecoration: 'none',
      borderBottom: `1px dashed ${theme.palette.primary.main}55`,
    },
    '& a:hover': {
      textDecoration: 'underline',
    },
    '& blockquote': {
      borderLeft: `4px solid ${theme.palette.primary.main}`,
      background: theme.palette.type === 'dark' ? 'rgba(255,255,255,0.05)' : 'rgba(0,0,0,0.03)',
      padding: theme.spacing(1, 2),
      margin: theme.spacing(2, 0),
      fontStyle: 'italic',
    },
    '& pre': {
      background: theme.palette.type === 'dark' ? '#111' : '#f5f5f5',
      color: theme.palette.text.primary,
      borderRadius: theme.shape.borderRadius,
      padding: theme.spacing(2),
      overflowX: 'auto',
    },
    '& code': {
      background: theme.palette.type === 'dark' ? 'rgba(255,255,255,0.08)' : 'rgba(0,0,0,0.06)',
      padding: '0.2em 0.4em',
      borderRadius: 4,
    },
    '& ul, & ol': {
      marginLeft: theme.spacing(3),
    },
    '& img': {
      maxWidth: '100%',
      borderRadius: theme.shape.borderRadius,
    },
    '& table': {
      width: '100%',
      borderCollapse: 'collapse',
      margin: theme.spacing(2, 0),
      borderRadius: theme.shape.borderRadius,
      overflow: 'hidden',
      border: `1px solid ${theme.palette.divider}`,
    },
    '& th': {
      backgroundColor: theme.palette.type === 'dark' 
        ? 'rgba(255, 255, 255, 0.08)'
        : 'rgba(0, 0, 0, 0.08)',
      padding: theme.spacing(1.5),
      textAlign: 'left',
      fontWeight: 600,
      borderBottom: `2px solid ${theme.palette.divider}`,
      color: theme.palette.text.primary,
    },
    '& td': {
      padding: theme.spacing(1.5),
      borderBottom: `1px solid ${theme.palette.divider}`,
      color: theme.palette.text.primary,
    },
    '& tr:nth-child(even)': {
      backgroundColor: theme.palette.type === 'dark' 
        ? 'rgba(255, 255, 255, 0.02)'
        : 'rgba(0, 0, 0, 0.02)',
    },
    '& tr:hover': {
      backgroundColor: theme.palette.type === 'dark' 
        ? 'rgba(255, 255, 255, 0.05)'
        : 'rgba(0, 0, 0, 0.05)',
    },
  },
  backButton: {
    marginTop: theme.spacing(4),
    borderRadius: 25,
    padding: '10px 20px',
    fontWeight: 'bold',
  },
  errorContainer: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'center',
    padding: theme.spacing(8, 0),
  }
}));

export const BlogPost = () => {
  const classes = useStyles();
  const { slug } = useParams();
  const post = blogManager.getPostBySlug(slug);
  const [markdownContent, setMarkdownContent] = useState('');
  
  // Extend sanitize schema to allow GFM tables, images, and code classes
  const sanitizeSchema = React.useMemo(() => {
    const schema = { ...defaultSchema };
    schema.tagNames = [
      ...(defaultSchema.tagNames || []),
      'table',
      'thead',
      'tbody',
      'tr',
      'td',
      'th',
      'del',
      'input'
    ];
    schema.attributes = {
      ...(defaultSchema.attributes || {}),
      table: [
        ...((defaultSchema.attributes && defaultSchema.attributes.table) || []),
        'className'
      ],
      td: [
        ...((defaultSchema.attributes && defaultSchema.attributes.td) || []),
        'colspan',
        'rowspan',
        'align'
      ],
      input: [
        'type',
        'checked',
        'disabled'
      ],
      th: [
        ...((defaultSchema.attributes && defaultSchema.attributes.th) || []),
        'colspan',
        'rowspan',
        'align'
      ],
      img: [
        ...((defaultSchema.attributes && defaultSchema.attributes.img) || []),
        'src',
        'alt',
        'title',
        'width',
        'height'
      ],
      code: [
        ...((defaultSchema.attributes && defaultSchema.attributes.code) || []),
        ['className', /^language-/]
      ]
    };
    return schema;
  }, []);

  useEffect(() => {
    const loadContent = async () => {
      if (post) {
        if (post.loadContent) {
          // Load content from markdown file
          const content = await blogManager.getPostContent(post.slug);
          setMarkdownContent(content || '');
        } else if (post.content) {
          setMarkdownContent(post.content);
        } else {
          // Generate placeholder content
          const content = blogManager.generatePlaceholderContent(post);
          setMarkdownContent(content);
        }
      }
    };

    loadContent();
  }, [post]);

  if (!post) {
    return (
      <div className={classes.root}>
        <LogoLink />
        <ThemeToggle />
        <Hidden smDown>
          <SocialIcons />
        </Hidden>
        <Hidden mdUp>
          <SpeedDials />
        </Hidden>
        <TopNavbar />
        <div className={classes.contentWrapper}>
          <Container maxWidth="md" className={classes.errorContainer}>
            <Typography variant="h4" gutterBottom>Post not found</Typography>
            <Typography variant="body1" paragraph>
              Sorry, the blog post you're looking for doesn't exist or has been removed.
            </Typography>
            <Button 
              component={Link} 
              to="/blog" 
              startIcon={<ArrowBack />}
              variant="contained"
              color="primary"
              className={classes.backButton}
            >
              Back to Blog
            </Button>
          </Container>
        </div>
      </div>
    );
  }

  return (
    <div className={classes.root}>
      <LogoLink />
      <ThemeToggle />
      <Hidden smDown>
        <SocialIcons />
      </Hidden>
      <Hidden mdUp>
        <SpeedDials />
      </Hidden>
      <TopNavbar />
      <div className={classes.contentWrapper}>
        <Container maxWidth="lg">
          <Paper className={classes.hero} elevation={3}>
            <Typography variant="h3" component="h1" gutterBottom>
              {post.title}
            </Typography>
            <div className={classes.meta}>
              <Avatar alt={post.author} src="/assets/profile.JPG" />
              <Typography variant="subtitle1">{post.author}</Typography>
              <Typography variant="subtitle2">{new Date(post.date).toLocaleDateString()}</Typography>
              {post.tags && post.tags.map((tag) => (
                <Chip key={tag} label={tag} className={classes.tag} />
              ))}
            </div>
            {post.image && (
              <Box style={{ textAlign: 'center', marginBottom: '16px' }}>
                <img src={`${process.env.PUBLIC_URL}${post.image}`} alt={post.title} className={classes.headerImage} />
              </Box>
            )}
            <Typography variant="h6">{post.summary}</Typography>
          </Paper>
          <Paper className={classes.content} elevation={1}>
            <ReactMarkdown 
              remarkPlugins={[remarkGfm]}
              rehypePlugins={[[rehypeSanitize, sanitizeSchema]]}
              linkTarget="_blank"
            >
              {markdownContent}
            </ReactMarkdown>
          </Paper>
          <Button 
            component={Link} 
            to="/blog" 
            startIcon={<ArrowBack />}
            className={classes.backButton}
            color="primary"
            variant="contained"
          >
            Back to Blog
          </Button>
        </Container>
      </div>
    </div>
  );
};

export default BlogPost; 
